/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState } from "react";
import SpotifyWebApi from "spotify-web-api-node";
import ArtistSide from "./ArtistSide";
import './Game.css';

type Artist = SpotifyApi.ArtistObjectSimplified;
type Side = 'left' | 'right';

export default function Game({ spotifyApi, artists }: { spotifyApi: SpotifyWebApi, artists: Artist[] }) {
    const [validIds, setValidIds] = useState<number[]>([])

    const [leftArtist, setLeftArtist] = useState<Artist>();
    const [leftArtistListeners, setLeftArtistListeners] = useState<number>();
    const [rightArtist, setRightArtist] = useState<Artist>();
    const [rightArtistListeners, setRightArtistListeners] = useState<number>();

    const [comparison, setComparison] = useState<'>' | '<'>('>');
    const [currentHover, setCurrentHover] = useState<Side>('left')

    const [sideClicked, setSideClicked] = useState<Side>('right');
    const [state, setState] = useState<'playing' | 'wrong' | 'right' | 'end'>('playing');
    const [currentStreak, setCurrentStreak] = useState<number>(0);
    const [bestStreak, setBestStreak] = useState<number>(0);

    const lockButtons = leftArtistListeners === undefined || rightArtistListeners === undefined;

    /**
     * Will pick a random artist from the valid ones, and set it as a side's artist. Will
     * also return a modified list of the index list, without the picked artist's index. This
     * allows the chaining of side updates, without relying on react updating the state variable
     * for the index list.
     * @param setSide The setter of the side to change
     * @param indexes A list containing all valid artist indexes
     * @returns The list of indexes without the index of the selected artist
     */
    const updateSide = (side: Side, indexes: number[]) => {
        const subIndex = Math.floor(Math.random() * indexes.length);
        const actualIndex = indexes[subIndex];
        indexes.splice(subIndex, 1);
        const artist = artists[actualIndex];
        if (side === 'left') {
            setLeftArtist(artist);
            setLeftArtistListeners(undefined);
        } else {
            setRightArtist(artist);
            setRightArtistListeners(undefined);
        }

        return indexes;
    };

    const reset = () => {
        if (artists.length < 2) return;

        let validIds = Array.from({ length: artists.length }, (v, k) => k);
        validIds = updateSide('left', validIds);
        validIds = updateSide('right', validIds);
        setValidIds(validIds);
        setComparison(Math.random() < 0.5 ? '<' : '>');
        setCurrentStreak(0);
        setState('playing');
    }
    useEffect(reset, [artists]);

    const clickedArtist = (side: Side) => {
        // Answer not yet defined
        if (leftArtistListeners === undefined || rightArtistListeners === undefined) {
            return;
        }
        // "Click to continue"
        if (state === 'right' || state === 'wrong') {
            continueGame();
            return;
        }

        const compResult = Math.sign(leftArtistListeners - rightArtistListeners) * (comparison === '<' ? 1 : -1);
        const correct = (compResult >= 0) !== (side === 'left');
        setState(correct ? 'right' : 'wrong');
        setSideClicked(side);

        if (correct) {
            setCurrentStreak(currentStreak + 1);
            if (currentStreak + 1 > bestStreak) {
                setBestStreak(currentStreak + 1);
            }
        } else {
            setCurrentStreak(0);
        }
    };

    const continueGame = () => {
        if (validIds.length <= 0) {
            setState('end');
            return;
        }
        const newValidIds = updateSide(sideClicked === 'left' ? 'right' : 'left', validIds);
        setValidIds(newValidIds);
        setComparison(Math.random() < 0.5 ? '<' : '>')
        setState('playing');
    }

    if (state === 'end') {
        return (
            <div className="container vh-100 d-flex align-items-center justify-content-center">
                <div className="col-lg-6 col-md-8 col-sm-12 p-2 d-flex flex-column align-items-center">
                    <h1>That's a wrap!</h1>
                    <h2>Best streak: <strong>{bestStreak}</strong>/{artists.length - 1}</h2>
                    <p>
                        And that's all the artists of this playlist! Thanks for playing!
                    </p>
                    <a href="/album/" className="btn btn-primary">
                        Choose another playlist
                    </a>
                </div>
            </div>
        );
    }

    if (!leftArtist || !rightArtist) {
        return (
            <p>Hang on...</p>
        );
    }

    return (
        <div className="row h-100">
            <div className="col-6 p-0" onMouseEnter={() => setCurrentHover('left')}>
                <ArtistSide
                    spotifyApi={spotifyApi}
                    artist={leftArtist}
                    listenerCallback={setLeftArtistListeners}
                    showStats={state === 'right' || state === 'wrong'}
                />
            </div>
            <div className="col-6 p-0" onMouseEnter={() => setCurrentHover('right')}>
                <ArtistSide
                    spotifyApi={spotifyApi}
                    artist={rightArtist}
                    listenerCallback={setRightArtistListeners}
                    showStats={state === 'right' || state === 'wrong'}
                />
            </div>
            <div className="question-block rounded text-center fs-2" onMouseMove={(e) => {
                setCurrentHover((e.clientX - e.currentTarget.offsetLeft) < e.currentTarget.clientWidth / 2 ? 'left' : 'right');
            }}>
                <div className="current-score">
                    Streak: {currentStreak} <span className="text-muted">({validIds.length} artists left)</span> <br />
                    Best streak: {bestStreak}
                </div>
                <div className="mb-4">
                    {state === 'playing' ? (
                        <> Who is <strong>{comparison === '<' ? 'less' : 'more'}</strong> popular?</>
                    ) : (
                        <>{state === 'right' ? 'Correct!' : 'Wrong!'} Click again to continue.</>
                    )}
                </div>
                <div className="answers">
                    <button className={`btn btn-light fs-3 ${lockButtons && 'disabled'}`} disabled={lockButtons} onClick={() => clickedArtist('left')}>
                        {leftArtist.name}
                    </button>
                    <div className={`question-symbol symbol-focus-${currentHover}`}>
                        {comparison}
                    </div>
                    <button className={`btn btn-light fs-3 ${lockButtons && 'disabled'}`} disabled={lockButtons} onClick={() => clickedArtist('right')}>
                        {rightArtist.name}
                    </button>
                </div>
            </div>
            <a className="btn btn-light exit-button" href="/album/">
                Exit
            </a>
        </div>
    );
}
