import { useEffect } from "react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { spotifyClientId } from "../helpers/Config";
import SpotifyWebApi from "spotify-web-api-node";
import Game from "../components/Game";

type Artist = SpotifyApi.ArtistObjectSimplified;

async function loadPlaylistArtists(
    spotifyApi: SpotifyWebApi,
    playlistId: string,
    progressCallback: (progress: number) => (any)
) {
    const artistIds = new Set<string>();
    const artists: Artist[] = [];
    let isFinished: boolean = false;
    let downloaded: number = 0;
    while (!isFinished) {
        const tracks = await spotifyApi.getPlaylistTracks(playlistId, { limit: 50, offset: downloaded });
        for (const track of tracks.body.items) {
            if (!track.track) continue;
            for (const artist of track.track.artists) {
                if (artistIds.has(artist.id)) continue;
                artists.push(artist);
                artistIds.add(artist.id);
            }
        }
        downloaded += tracks.body.limit;
        isFinished = tracks.body.next === null;
        progressCallback(downloaded / tracks.body.total * 100);
    }
    return artists;
}

export default function Play() {
    const [loaded, setLoaded] = useState<boolean>(false);
    const [loadingState, setLoadingState] = useState<number>(0);
    const [spotifyApi, setSpotifyApi] = useState<SpotifyWebApi | undefined>();
    const [artists, setArtists] = useState<Artist[]>([]);
    const navigate = useNavigate();

    useEffect(() => {
        const spotifyAccess = localStorage.getItem('spotify_access');
        const spotifyRefresh = localStorage.getItem('spotify_refresh');

        if (!spotifyAccess || !spotifyRefresh) {
            navigate('/spotify/');
            return;
        }

        const spotifyApi = new SpotifyWebApi({
            clientId: spotifyClientId,
            accessToken: spotifyAccess,
            refreshToken: spotifyRefresh,
        });
        setSpotifyApi(spotifyApi);

        const mode = localStorage.getItem('mode');
        if (mode === 'playlist') {
            const playlistId = localStorage.getItem('playlist_id');
            if (!playlistId) {
                navigate('/album/');
                return;
            }
            loadPlaylistArtists(spotifyApi, playlistId, setLoadingState).then((artists) => {
                setLoaded(true);
                setArtists([...artists]);
            });
        } else if (mode === 'followed') {
            alert('Quizz by followed is a not finished yet!');
        } else {
            navigate('/album/');
        }
    }, []);

    if (!loaded || !spotifyApi) {
        return (
            <div className="container vh-100 d-flex align-items-center justify-content-center">
                <div className="col-lg-6 col-md-8 col-sm-12 p-2 d-flex flex-column align-items-center">
                    <h1>Loading data...</h1>
                    <h2>{loadingState.toFixed(2)}%</h2>
                    <div className="progress w-100 m-3">
                        <div
                            className="progress-bar progress-bar-striped progress-bar-animated"
                            role="progressbar"
                            aria-valuenow={loadingState}
                            aria-valuemin={0}
                            aria-valuemax={100}
                            style={{ width: `${loadingState}%` }}
                        />
                    </div>
                    <p>
                        Not loading? Try <a href="/spotify/">logging in again</a>.
                    </p>
                </div>
            </div>
        );
    }

    if (artists.length < 2) {
        return (
            <div className="container vh-100 d-flex align-items-center justify-content-center">
                <div className="col-lg-6 col-md-8 col-sm-12 p-2 d-flex flex-column align-items-center">
                    <h1>Oops!</h1>
                    <p>
                        The playlist you selected has {artists.length} artists. It needs at least <strong>2 </strong>
                        artists to be playable !
                    </p>
                    {artists.length === 1 && (
                        <p>
                            Although I'm sure {artists[0].name} is great...
                        </p>
                    )}
                    <a href="/album/" className="btn btn-primary">
                        Choose another playlist
                    </a>
                </div>
            </div>
        );
    }

    return <Game spotifyApi={spotifyApi} artists={artists} />;
}
