import { useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import pkceChallenge from 'pkce-challenge';
import './SpotifyLogin.css'
import { useState } from 'react';
import { url, spotifyClientId } from '../helpers/Config';

export default function SpotifyLogin() {
    const [codeChallenge, setCodeChallenge] = useState('');
    const { search } = useLocation();
    const navigate = useNavigate();

    useEffect(() => {
        const params = new URLSearchParams(search);
        const code = params.get('code');
        const codeVerifier = localStorage.getItem('pkce_verifier');

        if (code && codeVerifier) {
            fetch('https://accounts.spotify.com/api/token', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
                },
                body: new URLSearchParams({
                    client_id: spotifyClientId,
                    grant_type: 'authorization_code',
                    code,
                    redirect_uri: `${url}/spotify/`,
                    code_verifier: codeVerifier,
                }),
            })
                .then(response => response.json())
                .then((data) => {
                    if (Object.keys(data).includes('access_token') && Object.keys(data).includes('refresh_token')) {
                        localStorage.setItem('spotify_access', data.access_token);
                        localStorage.setItem('spotify_refresh', data.refresh_token);
                        navigate('/album/');
                    }
                });
        } else {
            const pkce = pkceChallenge();
            localStorage.setItem('pkce_verifier', pkce.code_verifier);
            setCodeChallenge(pkce.code_challenge);
        }
    }, []);

    const spotifyUrl = 'https://accounts.spotify.com/authorize?' + new URLSearchParams({
        response_type: 'code',
        client_id: spotifyClientId,
        scope: 'playlist-read-private',
        redirect_uri: `${url}/spotify/`,
        code_challenge_method: 'S256',
        code_challenge: codeChallenge,
    }).toString();

    return (
        <div className="container d-flex justify-content-center">
            <div className="col-lg-6 col-md-8 col-sm-12 p-2">
                <div className="row d-flex justify-content-center">
                    <h1 className="text-center pt-5">Spotify Login</h1>
                    <p>
                        To play this game, login with Spotify! We won't do or store anything,
                        we just need permissions to look through your playlist real quick!
                    </p>
                    <a href={spotifyUrl} className="btn btn-primary w-auto spotify-button">
                        Log me in!
                    </a>
                </div>
            </div>
        </div>
    );
}
