import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { spotifyClientId } from "../helpers/Config";
import SpotifyWebApi from "spotify-web-api-node";

export default function PlaylistSelect() {
    const [spotifyApi, setSpotifyApi] = useState<SpotifyWebApi>();
    const [playlists, setPlaylists] = useState<SpotifyApi.PlaylistObjectSimplified[]>();
    const [canLoadMore, setCanLoadMore] = useState<boolean>();
    const navigate = useNavigate();

    useEffect(() => {
        const spotifyAccess = localStorage.getItem('spotify_access');
        const spotifyRefresh = localStorage.getItem('spotify_refresh');
        if (!spotifyAccess || !spotifyRefresh) {
            navigate('/spotify/');
            return;
        }
        setSpotifyApi(new SpotifyWebApi({
            clientId: spotifyClientId,
            accessToken: spotifyAccess,
            refreshToken: spotifyRefresh,
        }));
    }, []);

    useEffect(() => {
        if (spotifyApi) {
            spotifyApi.getUserPlaylists().then((response) => {
                setPlaylists(response.body.items);
                setCanLoadMore(response.body.next !== null);
            }).catch((reason) => {
                navigate('/spotify/');
            });
        }
    }, [spotifyApi])

    // Used to auto-load playlists when end of container reached
    const onScroll = (e: React.UIEvent<HTMLElement>) => {
        if (e.currentTarget) {
            const { scrollTop, scrollHeight, clientHeight } = e.currentTarget;
            if (scrollTop + clientHeight === scrollHeight && playlists && canLoadMore && spotifyApi) {
                spotifyApi.getUserPlaylists({ offset: playlists.length }).then((response) => {
                    setPlaylists([...playlists, ...response.body.items]);
                    setCanLoadMore(response.body.next !== null);
                })
            }
        }
    };

    const selectPlaylist = (playlist: SpotifyApi.PlaylistObjectSimplified) => {
        localStorage.setItem('mode', 'playlist');
        localStorage.setItem('playlist_id', playlist.id);
        console.log(`selected playlist ${playlist.id}`)
        navigate('/play/');
    };

    return (
        <div className="container d-flex justify-content-center">
            <div className="col-lg-6 col-md-8 col-sm-12 p-2" style={{ height: '75vh' }}>
                <h1 className="text-center pt-5">Choose a playlist</h1>
                <p>
                    Select a playlist from which artists will be picked for the game! You can also
                    play this with all your followed artists, if you want.
                </p>
                <div className="row pt-5 justify-content-center text-center">
                    <button className="btn btn-primary w-auto">
                        Followed Artists
                    </button>
                    <div className="divider my-3">or</div>
                    <h3>Playlists</h3>
                </div>
                {!playlists ? (
                    <p>Loading...</p>
                ) : (
                    <div className="d-flex flex-column overflow-scroll h-100" onScroll={onScroll}>
                        {playlists.map((playlist) => (
                            <div className="card my-2" key={playlist.id}>
                                <div className="row p-2">
                                    <div className="col-auto">
                                        <img
                                            src={playlist.images.at(-1)?.url ?? "/default-placeholder.png"}
                                            alt="playlist cover"
                                            className="rounded"
                                            style={{ width: '64px', height: '64px', objectFit: 'cover' }}
                                        />
                                    </div>
                                    <div className="col">
                                        {playlist.name}
                                        <span className="text-muted">
                                            &nbsp;- {playlist.owner.display_name}
                                        </span>
                                        <br />
                                        <span className="text-muted">
                                            {playlist.tracks.total} tracks
                                        </span>
                                    </div>
                                    <div className="col-auto">
                                        <button className="btn btn-secondary" onClick={() => selectPlaylist(playlist)} >
                                            Select
                                        </button>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
}
