import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './Welcome.css';

export default function Welcome() {
    const [spotifyKey, setSpotifyKey] = useState<string | null>(null);
    const [album, setAlbum] = useState<string | null>(null);
    const navigate = useNavigate();

    useEffect(() => {
        const spotifyKey_ = localStorage.getItem('spotify_key');
        if (spotifyKey_ === null) {
            navigate('/spotify/');
            return;
        }
        setSpotifyKey(spotifyKey_);

        const album_ = localStorage.getItem('album');
        if (album_ === null) {
            navigate('/album/');
            return;
        }
        setAlbum(album_);
    }, []);

    return (
        <>
            <h1>Welcome!</h1>
            <h2>Spotify key: {spotifyKey}</h2>
            <h2>Album: {album}</h2>
        </>
    );
}
