/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState } from "react";
import SpotifyWebApi from "spotify-web-api-node";
import DefaultImage from "./DefaultImage";
import FastAverageColor, { FastAverageColorResult } from 'fast-average-color';
import getMonthlyListeners from "../helpers/SpotifyExtraApi";
import CountUp from "react-countup";

type Artist = SpotifyApi.ArtistObjectSimplified;
type FullArtist = SpotifyApi.ArtistObjectFull;

export default function ArtistSide({
    spotifyApi, artist, listenerCallback, showStats,
}: {
    spotifyApi: SpotifyWebApi,
    artist: Artist,
    listenerCallback: (listeners: number | undefined) => any,
    showStats: boolean,
}) {
    const [fullArtist, setFullArtist] = useState<FullArtist>();
    const [monthlyListeners, setMonthlyListeners] = useState<number>();
    const [bgColor, setBgColor] = useState<FastAverageColorResult>();

    useEffect(() => {
        spotifyApi.getArtist(artist.id).then((response) => {
            setFullArtist(response.body);

            let image = response.body.images?.at(0)?.url;
            if (image) {
                const fac = new FastAverageColor();
                fac.getColorAsync(image, {
                    algorithm: 'dominant',
                    ignoredColor: [[255, 255, 255, 255, 25], [0, 0, 0, 255, 20]],
                }).then((color) => {
                    setBgColor(color);
                });
            }
        });
        getMonthlyListeners(artist.id).then((listeners) => {
            setMonthlyListeners(listeners);
            listenerCallback(listeners ?? 0);
        });

    }, [artist])

    if (!fullArtist) {
        return (
            <h2>
                Loading data of {artist.name}...
            </h2>
        );
    }

    return (
        <div
            className="col-12 p-5 h-100"
            style={{
                backgroundColor: bgColor?.hex ?? 'white',
                color: bgColor?.isDark ? 'white' : 'black',
                transition: 'all 0.5s ease-in-out',
            }}
        >
            <div className="d-flex flex-row">
                <DefaultImage
                    src={fullArtist.images.at(0)?.url}
                    className="rounded shadow-sm"
                    style={{
                        width: '320px',
                        height: '320px',
                        objectFit: 'cover',
                    }}
                />
                <div className="mx-3">
                    <h1>
                        {fullArtist.name}
                    </h1>
                    <h3 style={{ opacity: '50%' }}>
                        {fullArtist.genres.join(', ')}
                    </h3>
                    {showStats && (
                        <h3>
                            <CountUp
                                start={0}
                                end={monthlyListeners!}
                                duration={2}
                                separator={' '}
                            />
                        </h3>
                    )}
                </div>
            </div>
        </div>
    );
}

