const tokenMatcher = /"accessToken":"([\w_-]*)"/g;
const expirationMatcher = /"accessTokenExpirationTimestampMs":(\d*),/g;

async function getPublicAccessToken() {
    const spotifyPublicKey = localStorage.getItem('spotify_public_key');
    const spotifyPublicKeyExpiry = localStorage.getItem('spotify_public_key_expiry');
    if (spotifyPublicKey && spotifyPublicKeyExpiry && parseInt(spotifyPublicKeyExpiry) > Date.now()) {
        return spotifyPublicKey;
    }

    return await fetch(
        'https://n1ark.com/spotify_app'
    ).then((response) =>
        response.text()
    ).then((data) => {
        const expiration = expirationMatcher.exec(data)?.at(1);
        const token = tokenMatcher.exec(data)?.at(1);
        if (expiration && token) {
            localStorage.setItem('spotify_public_key', token);
            localStorage.setItem('spotify_public_key_expiry', expiration);
        }
        return token;
    });

}

async function getMonthlyListeners(artistId: string) {
    const token = await getPublicAccessToken();
    if (!token) {
        return undefined;
    }
    return await fetch(
        'https://api-partner.spotify.com/pathfinder/v1/query?' + new URLSearchParams({
            operationName: 'queryArtistOverview',
            variables: `{"uri":"spotify:artist:${artistId}"}`,
            extensions: '{"persistedQuery":{"version":1,"sha256Hash":"433e28d1e949372d3ca3aa6c47975cff428b5dc37b12f5325d9213accadf770a"}}',
        }).toString(), {
        headers: {
            Authorization: `Bearer ${token}`
        }
    }).then((response) =>
        response.json()
    ).then((data) => {
        console.log(data);
        console.log(data.data.artist.stats.monthlyListeners);
        return data.data.artist.stats.monthlyListeners as number;
    })
}

export default getMonthlyListeners;

