
export default function DefaultImage({
    src, alt, className, style,
}: {
    src: string | null | undefined,
    alt?: string,
    className?: string,
    style?: React.CSSProperties
}) {
    return (
        <img
            src={src ?? '/default-placeholder.png'}
            alt={alt}
            className={className}
            style={style}
        />
    );
}
